import { combineReducers } from 'redux';
import { persistReducer } from 'redux-persist';
import { all } from 'redux-saga/effects';
import storage from 'redux-persist/lib/storage';
import updateSoundList, {soundSaga} from './SoundList';
import setModal from './ModalResult';
import preferenceReducer, { preferenceSaga } from './PreferenceResult';
import updateTextList, { textSaga } from './TextList';
import updateLoginState from './LoginState';
import loading from './loading';
import setSensitivity, { sensitivitySaga } from './SensitivityResult';
import setDetecting, { detectingSaga } from './DetectingResult';

const config = {
    key: 'root',
    storage,
};

// root reducer  combine
//  reducer     
const rootReducer = combineReducers({
    updateSoundList,
    setModal,
    preferenceReducer,
    updateTextList,
    updateLoginState,
    loading,
    setSensitivity,
    setDetecting,
    //  ...
});

export function* rootSaga() {
    yield all([soundSaga(), sensitivitySaga(), preferenceSaga(), textSaga(), detectingSaga()]);
}

export default persistReducer(config, rootReducer);